/* Header.scss */
Header{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 64px;
    display: flex;
    flex-direction: column;
    color: #fff;
    padding: 0 15px;
    box-sizing: border-box;

    .status_bar{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        height: 37.5%;
        div{flex: 1;}
        div:nth-child(1){text-align: left;}
        div:nth-child(2){text-align: center;}
        div:nth-child(3){text-align: right;}
        div>*{padding: 3px;}

    }//status_bar
    
    .title_bar{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        height: 62.5%;

        * {
            flex: 1;
            font-size: 16px;
        }
        
        h1 {
            order: 2;
            text-align: center;

            span {
                display: inline-block;
            }
        }
        
        .left_item {
            order: 1;
            text-align: left;

            a {
                color: #fff;
                opacity: 0.5;
            }
        }
        
        .right_item {
            order: 3;
            text-align: right;
            font-size: 20px;

            a {
                color: #fff;
            }
        }
    }//title_bar
}//Header


